import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/mall/home"
    },{
      path: "/mall",
      name:"mall",
      component: () => import("@/pages/mall/index"),
      children:[{
        path: "home",
        name: 'mall_home',
        component: () => import("@/pages/mall/home/index")
      },{
        path: "city",
        name: "mall_city",
        component: () => import("@/pages/mall/city/index")
      },{
        path: "shopCar",
        name: "mall_shopCar",
        component: () => import("@/pages/mall/shopCar/index")
      },{
        path: "mine",
        name: "mall_mine",
        component: () => import("@/pages/mine/index")
      }]
    },{
      path:"/mall/order",
      name:"mall_order",
      component: () => import("@/pages/mall/shopCar/order")
    }
  ]
})



